import React, {useState} from 'react'
import {
  Checkbox,
  Container,
  ExpansionPanel,
  ExpansionPanelDetails,
  ExpansionPanelSummary,
  FormControlLabel,
  IconButton,
  InputBase,
  makeStyles,
  Typography,
  Card,
  Button,
  Box
} from '@material-ui/core';
import {
  AccessAlarm as DateIcon,
  AddCircleOutline as AddCircleOutlineIcon,
  Delete as DeleteIcon,
  Edit as EditIcon,
  ExpandMore as ExpandMoreIcon,
  Search as SearchIcon
} from '@material-ui/icons';


const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  root: {
    width: '100%',
    // maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  expensionSummary: {
    position: 'relative',
    display: 'block'
  },
  todoItemFormControl: {
    width: 'calc(100% - 180px)',
  },
  optionButtonArea: {
    position: 'fixed',
    outline: '0 !important',
    right: 85,
    // marginTop: '6px',
  },
  optionButtons: {
    outline: '0 !important',
    margin: 'auto'
  },
  dateField: {
    position: 'relative',
    outline: '0 !important',
    margin: 'auto',
    textAlign: 'center',
    marginRight: 30,
    marginTop: 10,
    border: '1px solid #ccc',
    padding: 10,
    borderRadius: 10,
    backgroundColor: '#00000010',
    fontSize: '0.875rem'
  },
  dateIcon: {
    position: 'absolute',
    top: -15,
    left: 'calc(50% - 12px)'
  },
  rootButton: {
    display: 'flex',
  },
  paper: {
    marginRight: theme.spacing(2),
  },
  optionButtonPopper: {
    zIndex: 9
  },
  filterArea: {
    marginBottom: 20,
    
  },
  search: {
    position: 'relative',
    borderRadius: 5,
    backgroundColor: '#fff',
    boxShadow: '0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12)',
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: 300,
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '20ch',
    }
  },
  addTodoItemButton: {
    display: 'block',
    outline: '0 !important',
    margin: 'auto',
    marginTop: 5
  },
  addTodoItemButtonIcon: {
    color: '#67af50',
    fontSize: 50
  },
}));

function TodoItemList(props) {
  const classes = useStyles();
  let lists = props.list.todoList;
  let filteredLists;
  
  const [searchText, setSearchText] = useState('');
  const [sortCompleted, setSortCompleted] = useState(false);
  const [sortIncomplete, setSortIncomplete] = useState(false);  

  let rem = []
  if(props.list.id){
    rem = props.list.todoList.filter((list) => !list.isComplete);
  }
  else {rem = []};


  if (props.list.id) {
    if(sortCompleted){
      filteredLists = props.list.todoList.filter((list)=> list.isComplete);
    }
    else if(sortIncomplete){
      filteredLists = props.list.todoList.filter((list)=> !list.isComplete);
    }
    else filteredLists = props.list.todoList.filter((list) => list.name.toLowerCase().includes(searchText.toLocaleLowerCase()));
  } else {
    filteredLists = [];
  }
  
  const sortByAll = () => {
    setSortIncomplete(false);
    setSortCompleted(false);
  }


  const twoDigitDateTextMaker = (time) => {
    let text = time.toString();
    if (text.toString().length > 1) {
      return text;
    } else {
      return '0' + text;
    }
  }
  
  const calculateFullDate = (time) => {
    // Return Type -> DD.MM.YY  HH:MM
    const eventDate = new Date(time);
    const resDate = twoDigitDateTextMaker(eventDate.getUTCDate()) + '.' + twoDigitDateTextMaker(eventDate.getUTCMonth() + 1) + '.' + twoDigitDateTextMaker(eventDate.getUTCFullYear());
    const resClock = twoDigitDateTextMaker(eventDate.getHours()) + ':' + twoDigitDateTextMaker(eventDate.getMinutes());
    return {
      date: resDate,
      clock: resClock
    };
  }
  
  const editTodo = (event, list, index) => {
    event.stopPropagation();
    props.openEditDialog(list, index);
  }
  
  const removeTodo = (event, list, index) => {
    event.stopPropagation();
    lists.splice(index, 1);
    props.updateTodoList({name: props.list.name, todoList: lists});
  }
  
  const checkboxToggle = (event, list) => {
    event.stopPropagation();
    list.isComplete = !list.isComplete;
    props.updateTodoList({name: props.list.name, todoList: lists});
  }
  
  
  if (props.list.id) {
    return (
      <Container maxWidth="lg" className={classes.container}>
        <Typography variant="h3" color="primary" style={{display: "flex", justifyContent:"center"}}>{props.list.name}</Typography>
        <div className={classes.filterArea}>
          <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchIcon style={{color:"lightgreen"}}/>
            </div>
            <InputBase
              placeholder="Search…"
              onChange={(text) => {
                setSearchText(text.target.value)
              }}
              value={searchText}
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{'aria-label': 'search'}}
            />
          </div>
          <Box display="flex" mt={2}>
            <Card elevation={2} style={{padding: "3px"}}>
            <Button color="secondary" onClick={sortByAll}>
                <Typography>All</Typography>
              </Button>
              <Button color="primary" onClick={() => {setSortCompleted(true); setSortIncomplete(false)}}>
                <Typography>Completed</Typography>
              </Button>
              <Button color="secondary" onClick={() => {setSortIncomplete(true) ; setSortCompleted(false)}}>
                <Typography>Active</Typography>
              </Button>
            </Card>
          </Box>
        </div>



            <Card style={{paddingTop:"5px", paddingBottom:"5px", display: "flex", justifyContent:"center"}} width={30} height={10} elevation={3}>
                <Typography variant="h6">Remaining Todues: <Typography style={{paddingLeft: '5px'}} display="inline" variant="h6" color="primary"> {rem.length}</Typography></Typography>
            </Card>




        {props.list.todoList.length > 0 &&
        <div className={classes.root}>
          {filteredLists.map((list, index) => {
            const fullDate = calculateFullDate(list.date);
            return (
              <ExpansionPanel key={index}>
                <ExpansionPanelSummary
                  expandIcon={<ExpandMoreIcon/>}
                  aria-label="Expand"
                  aria-controls="additional-actions1-content"
                  id="additional-actions1-header"
                >
                
                {window.screen.availWidth<500 ? 
                    <span style={{display:"flex", flexDirection:"column"}}>
                    <FormControlLabel
                      className={classes.todoItemFormControl}
                      aria-label="Acknowledge"
                      onClick={(event) => {
                        checkboxToggle(event, list);
                      }}
                      onFocus={(event) => event.stopPropagation()}
                      control={<Checkbox checked={list.isComplete}/>}
                      label={list.name}
                      style={{maxWidth:"300px", minWidth: "250px"}}
                    />
                    <span style={{display:"flex"}}>
                    <Typography className={classes.dateField}>
                      <DateIcon className={classes.dateIcon} color="secondary"/>
                      {fullDate.date}
                      <br/>
                      {fullDate.clock}
                    </Typography>
                    <IconButton aria-label="edit" className={classes.optionButtons} onClick={(event) => {
                      editTodo(event, list, index);
                    }}>
                      <EditIcon/>
                    </IconButton>
                    <IconButton aria-label="delete" className={classes.optionButtons} onClick={(event) => {
                      removeTodo(event, list, index);
                    }}>
                      <DeleteIcon/>
                    </IconButton>
                    </span>
                    </span>

                    :
                    <>
                    <FormControlLabel
                    className={classes.todoItemFormControl}
                    aria-label="Acknowledge"
                    onClick={(event) => {
                      checkboxToggle(event, list);
                    }}
                    onFocus={(event) => event.stopPropagation()}
                    control={<Checkbox checked={list.isComplete}/>}
                    label={list.name} />
                  <Typography className={classes.dateField}>
                    <DateIcon className={classes.dateIcon} color="secondary"/>
                    {fullDate.date}
                    <br/>
                    {fullDate.clock}
                  </Typography>
                  <IconButton aria-label="edit" className={classes.optionButtons} onClick={(event) => {
                    editTodo(event, list, index);
                  }}>
                    <EditIcon/>
                  </IconButton>
                  <IconButton aria-label="delete" className={classes.optionButtons} onClick={(event) => {
                    removeTodo(event, list, index);
                  }}>
                    <DeleteIcon/>
                  </IconButton>
                  </>
              }                
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                  <Typography color="textSecondary">
                    {list.detail}
                  </Typography>
                </ExpansionPanelDetails>
              </ExpansionPanel>
            );
          })}
        </div>
        }
        {props.list.todoList.length === 0 &&
        <div>
          <h5 style={{textAlign: 'center', padding: 25}}>There is no Todue Item in your list, you can add a new Todue Item with the button below.</h5>
        </div>
        }
        <IconButton aria-label="edit" className={classes.addTodoItemButton} onClick={(event) => {
          props.openNewTodoDialog();
        }}>
          <AddCircleOutlineIcon className={classes.addTodoItemButtonIcon}/>
        </IconButton>
      </Container>
    );
  } else {
    return (
      <div>
        <h3 style={{textAlign: 'center', padding: 25}}>No saved Todue List found, please first add Todue from left menu.
        Add your list.</h3>
      </div>
    )
  }
}

export default TodoItemList;
